*{
	margin: 0;
	padding: 0;
	box-sizing: content-box;
}

.App {
	width: max-content;
	background-color: #e9f0f8;
}

.branches-section {
	margin: 50px;
}

.alternate-bg {
	width: 1100px;
	margin: 0 auto;
}

.fade-in {
	width: min-content;
}

.cardContainer {
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.hoverItemContainer {
	width: 300px;
 	height: 370px;
	overflow: hidden;
	margin: 1.5em 3em;
	box-shadow: 0 5px 20px rgba(0,0,0,0.30), 0 20px 15px rgba(0,0,0,0.22);
	cursor: default;
	transition: .25s ease;
	transform: translateZ(0);
}

.hoverItemContainer:hover {
	height: 450px;
	transform: translateY(-10px) scale(1.05); /* Increase the scale and move the element up slightly */
	box-shadow: 0 25px 50px rgba(0,0,0,0.30), 0 20px 15px rgba(0,0,0,0.22); /* Adjust the box shadow as desired */
	z-index: 10000px;
  }

.hoverItemContainer:hover .hoverItemReadMore {
	margin-top: 0;
}

.hoverItemContentWrapper {
	background: #fff;
	margin: 0;
	padding: 0;
	position: relative;
	text-align: center;
	z-index: 10;
}

.hoverItemContainer:hover {
	transform: 1.1;
}

.hoverItemContentImage {
	width: 100%;
}

.hoverItemContentInfo {
	padding: 10px;
	margin: 0 !important;
	height: 25px;
	display: grid;
	flex-direction: row;
	justify-content: start;
	margin-top: -5px;
	padding-left: .5em;
	align-items: center;
	font-size: .75em;
	color: white;
}

.hoverItemContentTags {
	background: #0022ff;
	padding: .5em 1em;
	margin-right: 15px;
	color: white;
	cursor: pointer;
}

.hoverItemContentTitle {
	height: 106px;
	display: flex;
	justify-items: left;
	justify-content: left;
	padding: .25em;
	box-sizing: border-box;
	font-size: 13px;
}

.hoverItemContentTitleText {
	color: black;
	font-size: 1.25em;
	align-items: left;
}

.hoverItemReadMore {
	background: #0022ff;
	height: 50px;
	margin-top: -50px;
	display: flex;
	flex-direction: row;
	padding: .5em 2em;
	justify-content: space-between;
	align-items: center;
	box-sizing: border-box;
	cursor: pointer;
	transition: .25s ease;
	color: white;
}

.section-description {
  text-align: left;
  max-width: 70%;
  margin: auto;
  padding-top: 70px;

}

.section-title {
	color: #02f;
	font-family: Cairo,sans-serif;
	font-size: 42px;
	text-align: center;
	-webkit-font-smoothing: antialiased;
}

.slick-slide div {
	position: relative;
	margin: 0;
	display: block;
}

.slick-initialized {
	display: flex;
	justify-content: space-between;
}

.slick-slide {
	width: auto ;
}

.slick-prev:before, .slick-next:before {
	color: #02f;
}

.slider {
  height: 100vh;
  padding: 5rem;
  background: #000;
  color: #fff;
}

.card {
  height: 300px;
  background: #46e987;
}

.card h3 {
  text-align: center;
}